import React, { useCallback } from "react";
import Swiper from "swiper";

import CubeEffectSlide from "./components/CubeEffectSlide"
import { Slider } from "../Slider"
import { images } from "./data"
import { settings } from "./settings"

import styles from './CubeEffectSlider.module.scss';

const CubeEffectSlider = () => {
  const onClick = useCallback((swiper: Swiper) => {
    console.log(" ~ file: CubeEffectSlider.tsx:13 ~ onClick ~ swiper:", swiper)
  }, []);

  const param = {
    ...settings,
    onClick
  }
  return (
    <Slider
      {...param}
      items={images}
      renderItem={(item, index) => <CubeEffectSlide item={item} index={index} />}
      className={styles.cube}
    />
  )
}

export default CubeEffectSlider