import React, { useCallback, useEffect, useRef } from "react";

import { Image } from "../../../Image"

interface CubeEffectSlideProps {
  item: string;
  index: number | undefined;
}

const CubeEffectSlide = ({ item, index }: CubeEffectSlideProps) => {
  const slideRef = useRef<HTMLButtonElement>(null);

  const showAlert = useCallback(() => alert(`I'm slide ${index}`), []);

  useEffect(() => {
    slideRef.current?.addEventListener("click", showAlert);

    return () => slideRef.current?.removeEventListener('click', showAlert);
  }, []);

  return (
    <div style={{ height: '100%' }}>
      <button ref={slideRef}>Slide {index}</button>
      <Image src={item} />
    </div>
  )
}

export default CubeEffectSlide;